import Vue from 'vue'
import Vuex from 'vuex'
import about from './modules/about'
import store from './modules/store'
import home from './modules/home'
import test from './modules/test'

Vue.use(Vuex)

export default new Vuex.Store({
  modules: {
    about,
    store,
    home,
    test
  }
})
