<template>
  <div id="app">
    <div class="set content">
      <h2>设置Base URL</h2>
      <label>Base URL</label><input v-model="inputBaseURL"><br>
      <button @click="setBaseURL(inputBaseURL)">set BaseURL</button>
    </div>

    <div class="send content">
      <label>用户名：</label><input name="name" v-model="name" /><br>
      <label>密 码：</label><input name="password" v-model="password" /><br>
      <button @click="send">发送Ajax</button>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import { mapMutations, mapState } from 'vuex'
export default {
  name: 'TestPage',
  data () {
    return {
      inputBaseURL: '',
      name: 'Dragon',
      password: '12345678'
    }
  },
  computed: {
    ...mapState('test', ['BaseURL'])
  },
  methods: {
    ...mapMutations('test', ['setBaseURL']),
    send () {
      axios({
        url: `${this.BaseURL}/test`,
        method: 'POST',
        timeout: 5000,
        data: {
          name: this.name,
          password: this.password
        }
      }).then((response) => {
        this.message = response.data
        console.log(response)
      }).catch((error) => {
        console.log(error)
      })
    }

  }
}
</script>

<style scoped lang="less">
#app {
  .content{
    text-align: center;
    line-height: 100px;
    font-weight: bold;
    font-size: 20px;
    h2{
      margin: 0;
    }
    label{
      display: inline-block;
      width: 150px;
    }
    button{
      font-weight: bold;
      border: 0;
      border-radius: 10px;
      width: 200px;
      height: auto;
      padding: 10px 10px;
      background-color: #42B983;
      font-size: 20px;
      color: white;
    }
    input{
      margin-right: 30px;
      width: 200px;
      height: auto;
      font-size: large;
      border: rgb(218, 218, 218) solid 1px;
      border-radius: 5px;
      padding: 8px;
    }
  }
  .set{
    margin-top: 0px;
  }
}
</style>
